import styles from './CalendarSlider.module.css';
import {useContext, useState} from 'react';
import GlobalContext from '../../context/GlobalContext';
import dayjs from 'dayjs';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import {UserButton} from '../UI/Button';
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';
import {SmCalendarModal} from "../Modal/SmCalendarModal/SmCalendarModal";

export const CalendarSlider = () => {
  const {monthIndex, setMonthIndex} = useContext(GlobalContext);
  const [smCalendarShow, setSmCalendarShow] = useState(false);

  const handlePrevMonth = () => {
    setMonthIndex(monthIndex - 1);
  }

  const handleNextMonth = () => {
    setMonthIndex(monthIndex + 1);
  }

  const handleReset = () => {
    setMonthIndex(
      monthIndex === dayjs().month()
        ? monthIndex + Math.random()
        : dayjs().month()
    );
  }
  const showSmallCalendar = () => {
    setSmCalendarShow(true)
    console.log("MODAL")
  }

  return (
    <div className={styles.slider}>
      <UserButton className={styles.todayBtn} onClickHandler={handleReset}>
        Today
      </UserButton>
      <button className={styles.navBtn} onClick={handlePrevMonth}>
        <ChevronLeftIcon/>
      </button>
      <h2 className={styles.monthLabel}>
        {dayjs(new Date(dayjs().year(), monthIndex)).format('MMMM YYYY')}
      </h2>
      <button className={styles.navBtn} onClick={handleNextMonth}>
        <ChevronRightIcon/>
      </button>
      <UserButton className={styles.smCalendarBtn} onClickHandler={showSmallCalendar}>
        <CalendarMonthIcon className={styles.smCalendarIcon}/>
      </UserButton>
      <SmCalendarModal
        show={smCalendarShow}
        onHide={() => setSmCalendarShow(false)}
      />
    </div>
  );
};
